import { Component, OnInit, Input, Output, ViewChild, ElementRef, AfterViewInit  } from '@angular/core';
import { HttpEvent, HttpEventType, HttpClient , HttpHeaders } from '@angular/common/http';

@Component({
  selector: 'multi-uploader',
  templateUrl:'./multi-uploader.component.html',
 
  styleUrls: [
    './multi-uploader.component.scss'
  ]
})
export class MultiUploaderComponent implements OnInit, AfterViewInit {
  headers = new HttpHeaders();
  @ViewChild('fileUploader',null) fileUploader: ElementRef;
  @Input()config:any = {};
  files:any = [];
  previewFiles:any= [];
  allowedExt:any = [
    'jpg','jpeg','png','pdf','gif','txt'
  ];
  errors:any = [];
  constructor(private http: HttpClient  ) { }

  ngOnInit() {
    if (this.config) {
      if (!this.config.preview) {
        this.config.preview = false;
      }
      if (!this.config.type) {
        this.config.type = 'base64';
      }
    }
   
  }
  ngAfterViewInit() {
    // this.fileUploader = this.fileUploader.nativeElement;
  }
  clickOnUploader() {
    this.fileUploader.nativeElement.click();
  }
  uploadPreviewProcess() {
    if (this.fileUploader.nativeElement.files.length > 0) {
      this.files = [...this.fileUploader.nativeElement.files];
      this.files.map((file)=>{
        let f = new FileReader();
        f.onload = (e:any) =>
        {
          let ext = file.name.split('.').pop().toLowerCase();
          let icon = null;
          switch(ext) {
            case 'pdf':
              icon = 'pdf';
              break;
            case 'jpg':
            case 'jpeg':
            case 'png':
            case 'gif':
                icon = 'img';
                break;
            case 'wav':
            case 'mp3':
              icon = 'audio';
              break;
            default:
              icon = 'file';
              break;
          }
          this.previewFiles.push(
           {
             binaryFile:file,
             file: e.target.result,
             name: file.name,
             ext:icon,
             progress:0,
             uploadStatus:false
           }
          )
        }
        f.readAsDataURL(file);
      });
    }
  }
  removeFile(index:any) {
    this.previewFiles.splice(index,1);
  }
  uploadFile(index:any=null) {
    if(this.config.upload) {
      let uploadFile = this.previewFiles[index];
      let uploadRequest:any =null;
      if (this.config.upload.headers) {
        Object.keys(this.config.upload.headers).map((i)=>{
          this.headers.set(i, this.config.upload.headers[i]);
        })
      };
      let body = this.config.upload.body;
      if (this.config.type == 'base64') {
        body['file'] = uploadFile.file;
        body['file_name']  = uploadFile.name;
        body = JSON.stringify(body);
      } else if (this.config.type == 'binary') {
        const uploadData = new FormData();
        Object.keys(body).map((k)=>{
          uploadData.append(k, body[k]);
        });
        uploadData.append('file_name', uploadFile.name);
        uploadData.append('file', uploadFile.binaryFile);
        this.headers.set('Content-Type',undefined);
        body = uploadData;
      }

      if (this.config.upload.method.toLowerCase() === 'post') {
        uploadRequest =  this.http.post(this.config.upload.url,body, {
          reportProgress: true,
          observe: 'events',
          headers:this.headers});
      } else if (this.config.upload.method.toLowerCase() === 'put') {
        uploadRequest =  this.http.put(this.config.upload.url,body, {reportProgress: true,
          observe: 'events',headers:this.headers});
      }
      this.previewFiles[index].uploadStatus = true;
      uploadRequest.subscribe((event: HttpEvent<any>)=>{
        switch (event.type) {
          case HttpEventType.UploadProgress:
            this.previewFiles[index].progress = Math.round(event.loaded / event.total * 100);
            break;
          case HttpEventType.Response:
            setTimeout(() => {
              this.previewFiles[index].progress = 0;
            }, 1500);
            break;
            
        }
      });
    }
  }
  uploadAllFile() {
    this.previewFiles.forEach((file, index)=>{
      this.uploadFile(index);
    });
  }
  resetFileProcess() {
    this.previewFiles = [];
    this.files = [];
    this.fileUploader.nativeElement.value = null;
  }


}
